----- Variables -----
/* ----- Global ----- */
.body_container {
  display: -webkit-flex;
  display: flex;
  height: 88vh;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
  grid-template-rows: 1fr;
  font-family: "Raleway", sans-serif;
}

h3 {
  font-size: 0.7em;
  letter-spacing: 1.2px;
  color: yellow;
}

.photo-container img {
  -webkit-filter: drop-shadow(1px 1px 3px yellow);
  filter: drop-shadow(1px 1px 3px yellow);
  position: relative;
  width: 231px;
  height: 322px;
}

/* ----- Product Section ----- */

.product {
  display: grid;
  grid-template-columns: 0.9fr 1fr;
  margin: auto;
  padding: 2.5em 0;
  min-width: 600px;
  background-color: black;
  border-radius: 5px;
  color: white;
}

/* ----- Photo Section ----- */

.product__photo {
  position: relative;
}

.photo-container {
  position: absolute;
  left: -2.5em;
  display: grid;
  grid-template-rows: 1fr;
  width: 100%;
  height: 100%;
  border-radius: 6px;
  box-shadow: 4px 4px 25px -2px rgba(0, 0, 0, 0.3);
}

.photo-main {
  border-radius: 6px 6px 0 0;
  width: 64%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: 6px 6px 0 0;
  align-items: 6px 6px 0 0;
  background-color: #9be010;
  background: radial-gradient(#e5f89e, #96e001);
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
}

.photo-main .controls {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  padding: 0.8em;
  color: #fff;
}

.photo-main .photo-container img {
  position: absolute;
  left: -3.5em;
  top: 2em;
  max-width: 110%;
  -webkit-filter: saturate(150%) contrast(120%) hue-rotate(10deg) drop-shadow(1px 20px 10px rgba(0, 0, 0, 0.3));
  filter: saturate(150%) contrast(120%) hue-rotate(10deg) drop-shadow(1px 20px 10px rgba(0, 0, 0, 0.3));
}

.photo-album {
  padding: 0.7em 1em;
  border-radius: 0 0 6px 6px;
  background-color: #fff;
}

/* ----- Informations Section ----- */

.product__info {
  padding: 0.8em 0;
  position: relative;
  left: -27%;
}

.title h1 {
  margin-bottom: 0.1em;
  color: red;
  font-size: 1.5em;
  font-weight: 900;
}

.title span {
  font-size: 0.7em;
  color: yellow;
}

.price {
  margin: 1.5em 0;
  color: #ff3f40;
  font-size: 1.2em;
}

.price span {
  padding-left: 0.15em;
  font-size: 2.9em;
}

.variant {
  overflow: auto;
}

.variant h3 {
  margin-bottom: 1.1em;
}

.variant li {
  float: left;
  width: 35px;
  height: 35px;
  padding: 3px;
  border: 1px solid transparent;
  border-radius: 3px;
  cursor: pointer;
}

.variant li:first-child, .variant li:hover {
  border: 1px solid yellow;
}

.variant li:not(:first-child) {
  margin-left: 0.1em;
}

.description {
  clear: left;
  margin: 2em 0;
}

.description h3 {
  margin-bottom: 1em;
}

.description ul {
  font-size: 0.8em;
  list-style: disc;
  margin-left: 1em;
}

.description li {
  text-indent: -0.6em;
  margin-bottom: 0.5em;
}

.buy--btn {
  padding: 1.5em 3.1em;
  border: none;
  border-radius: 7px;
  font-size: 0.8em;
  font-weight: 700;
  letter-spacing: 1.3px;
  color: #fff;
  background-color: #ff3f40;
  box-shadow: 2px 2px 25px -7px #4c4c4c;
  cursor: pointer;
}

.buy--btn:active {
  -webkit-transform: scale(0.97);
  transform: scale(0.97);
}

/* ----- Footer Section ----- */

footer {
  padding: 1em;
  text-align: center;
  color: #fff;
}

footer a {
  color: #4c4c4c;
}

footer a:hover {
  color: #ff3f40;
}

